@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{margin:0;padding:0;width:100%;height:100%}

.App{display:flex;background-color:white;font-family:"Poppins", sans-serif}


#jumbotron{width:100%;height:auto;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;flex-direction:column;align-items:center;position:relative;background-image:linear-gradient(90deg, #8c8eff, #82cdff)}#jumbotron::before{background-image:url(/who-are-you/static/media/meeting.470013a3.jpg);background-repeat:no-repeat;background-size:cover;content:"";display:block;position:absolute;top:0;bottom:0;left:0;right:0;opacity:0.05}#jumbotron #navbar{position:relative}#jumbotron .user-img{width:auto;height:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;position:relative;width:30rem;margin:5rem 0 2rem 0}#jumbotron .user-name{width:auto;height:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;position:relative;font-size:3rem;font-weight:500;color:#fff}#jumbotron .user-job{width:auto;height:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:0 0 6rem 0;position:relative;font-size:1.5rem;font-weight:200;color:#fff}

#navbar{display:flex;justify-content:center;align-items:center;width:100%;height:auto;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;box-sizing:border-box;justify-content:space-between;background-color:rgba(0,0,0,0.3);padding:0.5rem 1rem}#navbar .btn-img{width:10rem}

.btn-img{width:auto;height:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:1rem}

.btn-default{display:flex;justify-content:center;align-items:center;width:auto;height:3rem;margin:1rem;padding:0rem 2rem;color:white;background-color:black;text-decoration:none;border-radius:50px;white-space:nowrap}.btn-border{display:flex;justify-content:center;align-items:center;width:auto;height:3rem;margin:1rem;padding:0rem 2rem;color:white;background-color:none;text-decoration:none;border:solid 1px white;border-radius:50px;white-space:nowrap}.btn-blank{width:auto;height:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:1rem;color:#ffffff;text-decoration:none;white-space:nowrap}

.btn-wrapper-horizontal{display:flex;flex-direction:row;justify-content:center;width:auto;height:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}

#user-info-sections{width:100%;height:auto;display:flex;align-items:center;flex-direction:column}

.section-wrapper{display:flex;flex-direction:column;align-items:center}.section-wrapper .section-title{font-size:1.75rem;color:#444;width:500px;margin:4rem 0 0rem 0}.section-wrapper #horizontal-line{height:1px;border-top:1px solid #999;width:600px;margin:1rem 0}.section-wrapper .section-list{width:500px;color:#555;font-size:1.25rem}.section-wrapper .section-list li{width:auto;height:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;white-space:nowrap;list-style-type:none}

